body {
  font-family: sans-serif;
}

#hider {
  display: none;
}

#hider:checked + .task {
  display: none;
}

.task {
  padding: 50px 150px;
}

.title {
  margin: 0 auto;
}

.task .task__step {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.task__step {
  flex: 1;
}

span {
  font-weight: bold;
}

#detail {
  display: block;
  width: 1000px;
  height: 500px;
  border: 2px solid;
  margin: 40px auto;
}
